import Accordion from 'accordion-js';

new Accordion('.accordion-container', {
  duration: 400,
  triggerClass: 'ac-trigger',
  panelClass: 'ac-panel',
  activeClass: 'is-active',
  showMultiple: true,
  heightStyle: 'content',
});
document.querySelectorAll('.ac').forEach(ac => {
  const trigger = ac.querySelector('.ac-trigger');
  const panel = ac.querySelector('.ac-panel');

  trigger.addEventListener('click', () => {
    if (ac.classList.contains('is-active')) {
      // Ð²Ñ–Ð´ÐºÑ€Ð¸Ð²Ð°Ñ”Ð¼Ð¾
      panel.style.maxHeight = panel.scrollHeight + "px";
      setTimeout(() => { panel.style.maxHeight = "none"; }, 400); // Ñ‰Ð¾Ð± Ð¿Ñ–ÑÐ»Ñ Ð°Ð½Ñ–Ð¼Ð°Ñ†Ñ–Ñ— Ð²Ð¸ÑÐ¾Ñ‚Ð° Ð±ÑƒÐ»Ð° Ð°Ð²Ñ‚Ð¾
    } else {
      // Ð·Ð°ÐºÑ€Ð¸Ð²Ð°Ñ”Ð¼Ð¾
      panel.style.maxHeight = panel.scrollHeight + "px";
      setTimeout(() => { panel.style.maxHeight = "0"; }, 10);
    }
  });
});