document.addEventListener('DOMContentLoaded', () => {
  const burgerBtn = document.querySelector('[data-header-menu-open]');
  const closeBtn = document.querySelector('[data-header-menu-close]');
  const mobileMenu = document.querySelector('.mobile-menu-js');
  const categoryLinks = document.querySelectorAll('[data-category-link]');
  // Ð’Ñ–Ð´ÐºÑ€Ð¸Ñ‚Ñ‚Ñ Ð¼ÐµÐ½ÑŽ
  burgerBtn.addEventListener('click', () => {
    mobileMenu.classList.add('open');
  });

  // Ð—Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ Ð¼ÐµÐ½ÑŽ
  closeBtn.addEventListener('click', () => {
    mobileMenu.classList.remove('open');
  });
  // ÐšÐ»Ñ–Ðº Ð¿Ð¾ ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ñ–Ñ—
  categoryLinks.forEach(link => {
    link.addEventListener('click', e => {
      e.preventDefault(); // Ð©Ð¾Ð± Ð½Ðµ Ð±ÑƒÐ»Ð¾ Ð¼Ð¸Ñ‚Ñ‚Ñ”Ð²Ð¾Ð³Ð¾ Ð¿ÐµÑ€ÐµÑ…Ð¾Ð´Ñƒ

      const targetId = link.getAttribute('href')?.replace('#', '');
      const targetSection = document.getElementById(targetId);

      if (targetSection) {
        mobileMenu.classList.remove('open');

        // ÐœÐ¸Ñ‚Ñ‚Ñ”Ð²Ð¸Ð¹ Ð¿ÐµÑ€ÐµÑ…Ñ–Ð´ Ð´Ð¾ ÑÐµÐºÑ†Ñ–Ñ—
        targetSection.setAttribute('tabindex', '-1');
        targetSection.focus();
      }
    });
  });
});
